import React, { useState } from "react";
import ChatBot from "react-simple-chatbot";
import axios from "axios";
import ad from "../images/logo1.png";

const SimpleForm = () => {
  const [inputData, setinputData] = useState({});

  // const saveData = async () => {
  //   const { data } = await axios.post("root/register", inputData);
  //   console.log(data.message);
  // };
  const handleSubmit = async () => {
    console.log("ending");
    const { data } = await axios.post(
      "https://adeventregistration.herokuapp.com/register",
      inputData
    );
    console.log(data);
  };

  const showVal = () => {
    console.log(inputData);
  };
  return (
    <ChatBot
      style={{
        margin: "auto",
        width: "90vw",
        marginTop: "10vh",
        backgroundImage: `url(${ad})`,
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center",
        backgroundSize: "contain",
        marginBottom: "5%",
      }}
      headerTitle="Event Registration Form"
      handleEnd={handleSubmit}
      speechSynthesis={{
        enable: true,
        lang: "en",
      }}
      // recognitionEnable={true}
      // recognitionPlaceholder="Listening..."
      steps={[
        {
          id: "fullname",
          message: "What is your name?",
          trigger: "name",
        },
        {
          id: "name",
          user: true,
          validator: (value) => {
            const tempData = inputData;
            tempData.name = value;
            setinputData(tempData);
            showVal();
            return true;
          },
          trigger: "end-message",
        },
        {
          id: "hello",
          message: "Hello {previousValue}, Nice to meet you!!",
          trigger: "roll",
        },
        {
          id: "roll",
          message: "What is your roll number",
          trigger: "roll-asked",
        },
        {
          id: "roll-asked",
          user: true,
          validator: (value) => {
            const tempData = inputData;
            tempData.roll = value;
            setinputData(tempData);
            showVal();
            if (value.length !== 9) {
              return "Please enter valid roll number";
            }
            return true;
          },
          trigger: "year-asked",
        },
        {
          id: "year-asked",
          message: "Which year you are in? ( 1 or 2 )",
          trigger: "year",
        },
        {
          id: "year",
          user: true,
          validator: (value) => {
            const tempData = inputData;
            tempData.year = value;
            setinputData(tempData);
            showVal();
            if (value !== "1" && value !== "2") {
              return "Please enter valid year";
            }
            return true;
          },
          trigger: "email",
        },
        {
          id: "email",
          message: "What is your nist email id?",
          trigger: "email-asked",
        },
        {
          id: "email-asked",
          user: true,
          validator: (value) => {
            const tempData = inputData;
            tempData.email = value;
            setinputData(tempData);
            showVal();
            return true;
          },
          trigger: "phone",
        },
        {
          id: "phone",
          message: "What is your phone number?",
          trigger: "phone-asked",
        },
        {
          id: "phone-asked",
          user: true,
          validator: (value) => {
            if (isNaN(value)) {
              return "Please enter a valid phone number";
            } else if (value.length < 10) {
              return "Please enter a valid phone number";
            } else if (value.length > 10) {
              return "Please enter a valid phone number";
            }
            const tempData = inputData;
            tempData.phone = value;
            setinputData(tempData);
            showVal();
            return true;
          },
          trigger: "whatsapp",
        },
        {
          id: "whatsapp",
          message: "What is your whatsapp number?",
          trigger: "whatsapp-asked",
        },
        {
          id: "whatsapp-asked",
          user: true,
          validator: (value) => {
            if (isNaN(value)) {
              return "Please enter a valid phone number";
            } else if (value.length < 10) {
              return "Please enter a valid phone number";
            } else if (value.length > 10) {
              return "Please enter a valid phone number";
            }
            const tempData = inputData;
            tempData.whatsapp = value;
            setinputData(tempData);
            showVal();
            return true;
          },
          trigger: "branch-asked",
        },
        {
          id: "branch-asked",
          message: "What your branch?",
          trigger: "branch",
        },
        {
          id: "branch",
          user: true,
          validator: (value) => {
            const tempData = inputData;
            tempData.branch = value;
            setinputData(tempData);
            showVal();
            return true;
          },
          trigger: "event-asked",
        },
        {
          id: "event-asked",
          message: "Enter your preferred event:",
          trigger: "event1",
        },
        {
          id: "event1",
          message: "ART-O-RAMA",
          trigger: "event2",
        },
        {
          id: "event2",
          message: "FILM-O-PHILIA",
          trigger: "event3",
        },
        {
          id: "event3",
          message: "OR BOTH",
          trigger: "event",
        },
        {
          id: "event",
          user: true,
          validator: (value) => {
            const tempData = inputData;
            tempData.event = value;
            setinputData(tempData);
            showVal();
            return true;
          },
          trigger: "end-message",
        },
        {
          id: "end-message",
          message:
            "Hey {previousValue}, sorry we are not accepting any responses any more if you still want to you can contact on the given numbers.",
          // end: true,
          trigger: "end-1",
        },
        {
          id: "end-1",
          message: "Vivek - 8789957606",
          // message: "Pooja - 9348404883",
          // end: true,
          trigger: "end-2",
        },
        {
          id: "end-2",
          message: "Pooja - 9348404883",
          // end: true,
          trigger: "end-3",
        },
        {
          id: "end-3",
          message: "Stay Tuned for upcoming events!!",
          end: true,
        },
      ]}
    />
  );
};

export default SimpleForm;
