import React from "react";
import Box from "@mui/material/Box";
import logo from "../images/event_logo.png";

const Content = () => {
  return (
    <div className="content">
      <img
        src={logo}
        alt="logo"
        style={{
          width: "10vw",
          // padding: "5px 0 5px 20px",
          margin: "10px 0 0 30px",
          boxShadow: "0px 0px 5px #000",
          borderRadius: "50%",
        }}
      />
      <Box
        sx={{
          maxWidth: "800px",
          padding: "30px",
          fontSize: "20px",
        }}
        className="box"
      >
        Welcome to the fun event of{" "}
        <b>
          <i>
            <span style={{ color: "#e63535" }}>Art</span>
          </i>
        </b>{" "}
        and{" "}
        <i>
          <b>Drama</b>.
        </i>
        <br />
        <br /> We are now here ready to make your existence of artist within
        youself to set out to sparkle. We are back with two one of kind events
        for artist and creative people all around a open theme competitions
        within time limits and certain exciting rounds.
        <br />
        <br /> Please fill up the Required Details. We will very shortly contact
        you based on the provided information with the Event Details and
        Submission link.
        <br />
        <br /> The Event will have 2 Rounds in total:
        <br />
        <br /> Round-1: Audience Choice( Post your Work on your Insta Handle and
        Tag us + Send us your Submission)
        <br />
        <br /> Round-2: The Winners of the First Round will be given specific
        topic and will be judged based on the participants execution of the
        topic given.
        <br />
        <br /> Once Judging is over..... Winners will be awarded with exciting
        prizes.
        <br />
        <br /> Don't let Hesitancy hold you back. Just Participate and let the
        world know of the Creative Person hidden inside you.
        <br />
        <br /> Thank You....😁😁
      </Box>
    </div>
  );
};

export default Content;
